.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
    padding: 50px;
    padding-top: 70px;
    width: 80%;

}

.movies .movie {
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #b2b2bc;
    box-shadow: 5px 5px 2px 1px rgba(138, 138, 150, 0.2);
}

.movie img {
    position: relative;
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
}

.movie .movie__title,
.movie .movie__year {
    margin: 0;
    font-weight: 300;
}

.movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0, 5;
    display: flex;
    margin: 5px;
}

.movie__genres li {
    margin-right: 10px;
    font-size: 14px;
}

.movie__summary {
    color: #3d3d5b;
}

